.home {
  height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("89787.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: color-burn;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #ffffff;
  position: relative;
}

.home h1 {
  font-size: 5rem;
}

.home h2 {
  font-size: 3rem;
  margin: 10px 0;
}

.home .btn {
  background-color: #e74c3c;
  color: #ffffff;
  padding: 25px 50px;
  font-size: 1.9rem;
  border: none;
  border-radius: 10px;
  box-shadow: 5px 0.5rem 5px #2c3e50;
  cursor: pointer;
  margin-top: 20px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: color 200ms ease;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #ffffff;
    transition: transform 0.3s ease;
    transform: translateX(100%);
    z-index: -1;
    border-radius: 10px;
  }

  &:hover::before {
    transform: translateX(0);
  }

  &:hover {
    color: #000;
    font-weight: 700;
  }
}

@media (max-width: 480px) {
  .home h1 {
    font-size: 2.5em;
  }

  .home h2 {
    font-size: 1.5em;
  }

  .home .btn {
    font-size: 1em;
    padding: 8px 16px;
  }
}
