footer {
  background-color: #2c3e50;
  color: #ffffff;
  padding: 20px 0;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
}

.footer-column {
  margin-bottom: 20px;
  flex: 1;
  min-width: 200px;
}

.footer-column h4 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.footer-column p,
.footer-column a {
  font-size: 1em;
  color: #ffffff;
  margin-bottom: 10px;
}

.footer-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-column ul li {
  margin-bottom: 10px;
}

.footer-column ul li a {
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-column ul li a:hover {
  color: #e74c3c;
}

.social-icons {
  margin-top: 10px;
}

.social-icons a {
  margin: 0 10px;
  color: #ffffff;
  font-size: 1.5em;
  transition: color 0.3s;

  &:hover {
    color: #e74c3c;
  }
}

.copyright {
  width: 100%;
  text-align: center;
  margin-top: 20px;
  font-size: 0.9em;
}
