.careers {
  background-color: #f4f4f4;
  padding: 100px 0;
  text-align: center;
}

.careers h2 {
  font-size: 3rem;
  margin-bottom: 10px;
  color: #333;
}

.careers p {
  font-size: 1.5rem;
  margin-bottom: 40px;
  color: #555;
}

.job-listings {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 20px;
}

.job-item {
  background-color: #ffffff;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  flex: 1;
  max-width: 450px;
  text-align: left;
}

.job-item h3 {
  font-size: 2rem;
  margin-bottom: 15px;
  color: #116dc9;
}

.job-item p {
  font-size: 1.5rem;
  margin-bottom: 20px;
  color: #666;
}

.apply-button {
  display: inline-block;
  background-color: #3498db;
  color: #ffffff;
  font-size: 1.4rem;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s;

  &:hover {
    background-color: #2980b9;
  }
}

@media (max-width: 480px) {
  .job-listings {
    flex-direction: column;
  }

  .job .footer .column h4 {
    font-size: 1.2em;
  }

  .job-listings {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
