.about {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 50px;
  background-color: #ecf0f1;
}

.about-image {
  margin-right: 10px;
}

.about-image img {
  width: 100%;
  max-width: 1500px;
  border-radius: 10px;
}

.about-content {
  padding: 20px;
}

.about-content h3 {
  font-size: 3rem;
  margin-bottom: 20px;
  color: #2c3e50;
}

.about-content p {
  font-size: 1.7rem;
  margin-bottom: 20px;
}

.about-content ul {
  list-style: none;
  padding: 0;
}

.about-content ul li {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.about-content ul li strong {
  color: #e74c3c;
}

@media (max-width: 768px) {
  .about {
    flex-direction: column;
    text-align: center;
  }

  .about .content {
    width: 100%;
    padding: 0;
  }

  .about img {
    margin-top: 20px;
    width: 55%;
  }

  .about-content p,
  .about-content ul li {
    text-align: left;
  }

  .about {
    padding: 20px 20px;
  }
}
