.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #2c3e50;
  color: #ffffff;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
}

.navbar .logo {
  font-size: 2em;
  font-weight: bold;
  font-family: Georgia, "Times New Roman", Times, serif;
}

.navbar ul {
  display: flex;
  font-size: 2rem;
  justify-content: space-evenly;
  width: 30vw;
}

.navbar ul li a {
  color: #ffffff;
  font-size: 1em;
  transition: color 0.3s;
  position: relative;

  &::after {
    content: "";
    width: 0;
    height: 3px;
    background: #e74c3c;
    position: absolute;
    left: 0;
    bottom: -5px;
    transition: 0.5s;
  }

  &:hover::after {
    width: 100%;
  }

  &:hover {
    color: #e74c3c;
  }
}

.menu-icon {
  font-size: 2.5rem;
  color: #ffffff;
  display: none;
  transition: transform 0.3s ease-in-out;
  margin-right: 2rem;
}

.menu-icon.fa-xmark {
  transform: rotate(90deg);
}

@media (max-width: 768px) {
  .navbar ul {
    flex-direction: row;
  }

  .navbar .nav {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0;
    border-top: solid 3px #1e1e1f;
    background-color: #2c3e50;
    z-index: 100;
    overflow: hidden;
    transition: height 0.5s ease;
    opacity: 0;
    padding: 10px;
  }

  .navbar .nav.active {
    /* display: flex; */
    bottom: -85%;
    opacity: 1;
    height: auto;
  }

  .menu-icon {
    display: block;
  }
}
