* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  text-decoration: none;
  list-style: none;
  outline: none;
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
  font-size: 68%;
}

body {
  font-family: "Arial", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1.6;
  background-color: #ecf0f1;
  color: #2c3e50;
}

section {
  min-height: 100vh;
  overflow: hidden;
}

@media (max-width: 768px) {
  html {
    font-size: 60%;
  }
}
